import React from "react";
import "./Works.css";
import { motion } from "framer-motion";
import { Switch, Route, Redirect, useLocation } from "react-router-dom";

//"./ali/work/work-player-big.png";

const containerVariant = {
  hidden: {
    x: "100vw",
    opacity: 0,
  },
  visible: {
    x: 0,
    opacity: 1,
    transition: { type: "tween", duration: 0.7 },
    when: "beforeChildren",
  },
  exit: {
    x: "-100vw",
    transition: {
      ease: "easeInOut",
    },
  },
};

function Ali() {
  const location = useLocation();


  return (
    <div className="work-container" id="visits">
      <motion.div
        className="work"
        variants={containerVariant}
        initial="hidden"
        animate="visible"
        exit="exit"
      >
        <div className="kanyewest">
          <iframe
            title="kanye"
            scrolling="no"
            className="kanyewest-iframe"
            src="https://kanyewestquoteland.netlify.app/"
            frameBorder="0"
          ></iframe>
        </div>
      </motion.div>

      <div className="favoritewebsites">
        <iframe
          title="favorite"
          scrolling="no"
          className="favoritewebsites-iframe"
          src="https://favoritewebsites.netlify.app/"
          frameBorder="0"
        ></iframe>
      </div>
    </div>
  );
}
export default Ali;
